<template>
	<view class="container mms-page">
		<view class="section">
			<view class="section-info">
				<view>物料名称：<view class="list-item-info">{{materialsData.materialsName}}</view></view>
				<view>物料编号：<view class="list-item-info">{{materialsData.materialsNo}}</view></view>
				<view>规格型号：<view class="list-item-info">{{materialsData.materialsModel}}</view></view>
				<view>库存数量：<view class="list-item-info">{{materialsData.inStockNum}} {{materialsData.materialsUnit}}</view></view>
			    
			</view>
		</view>
		<view class="mms-content">
			<uni-segmented-control :current="segmCurrent" :values="segmItems" @clickItem="onClickItem" styleType="button" activeColor="#6d3b85"></uni-segmented-control>
			        <view class="content">
						<view v-show="segmCurrent === 0">
							<view class="mms-list" v-if="materialList.length > 0">
								<view class="list-item" v-for="item in materialList" :key="item.id" v-if="item.locationNum > 0">
									<view>物料编号：<view class="list-item-info">{{item.materialsNo}}</view></view>
									<view>存放位置：<view class="list-item-info">{{item.storehouseName}}</view></view>
									<view>当前库存：<view class="list-item-info">{{item.locationNum}} {{item.materialsUnit}}</view></view>
									<view class="list-item-detail" @click="onOpenDetail(item,1)"> 转移 </view>
									
								</view>
							</view>
							<view  class="mms-list" v-else>
								<view class="record-nolist">没有可显示的数据</view>
							</view>
						</view>
			            <view v-show="segmCurrent === 1">
			                <view class="mms-list" v-if="inOrderList.length > 0">
			                		<view class="list-item"  v-for="item in inOrderList" :key="item.code">
			                			<view>当前库存：<view class="list-item-info">{{item.currentInStockNum}} {{item.materialsUnit}}</view></view>
			                			<view>入库数量：<view class="list-item-info">{{item.wareHousingNum}} {{item.materialsUnit}}</view></view>
			                			<view>存放位置：<view class="list-item-info">{{item.storehouseName}}</view></view>
			                			<view>登记人员：<view class="list-item-info">{{item.operator}} </view></view>
			                			<view>所属部门：<view class="list-item-info">{{item.dept}}</view></view>
			                			<view>备注：<view class="list-item-info">{{item.remark}}</view></view>
			                			<view>入库日期：<view class="list-item-info">{{item.wareHousingTime}}</view></view>
			                			<view>创建时间：<view class="list-item-info">{{item.createTime}}</view></view>
			                		</view>
			                </view>
							<view  class="mms-list" v-else>
								<view class="record-nolist">没有可显示的数据</view>
							</view>
			            </view>
			            <view v-show="segmCurrent === 2">
			                <view class="mms-list" v-if="outOrderList.length > 0">
			                		<view class="list-item"  v-for="item in outOrderList" :key="item.code">
			                			<view>当前库存：<view class="list-item-info">{{item.currentInStockNum}} {{item.materialsUnit}}</view></view>
			                			<view>出库数量：<view class="list-item-info">{{item.outOfStockNum}} {{item.materialsUnit}}</view></view>
			                			<view>存放位置：<view class="list-item-info">{{item.storehouseName}}</view></view>
			                			<view>登记人员：<view class="list-item-info">{{item.operator}} </view></view>
			                			<view>所属部门：<view class="list-item-info">{{item.dept}}</view></view>
			                			<view>备注：<view class="list-item-info">{{item.remark}}</view></view>
					                    <view>出库日期：<view class="list-item-info">{{item.outOfStockTime}}</view></view>
			                			<view>创建时间：<view class="list-item-info">{{item.createTime}}</view></view>
			                		</view>
			                </view>
							<view  class="mms-list" v-else>
								<view class="record-nolist">没有可显示的数据</view>
							</view>
			            </view>
			        </view>
		</view>
		<uni-popup ref="popup" type="dialog">
		    <view class="section">
				<uni-forms :modelValue="materialsInfo">
					<uni-forms-item name="hobby" label="存放位置">
						<uni-easyinput disabled type="text" v-model="materialsInfo.storehouseName" placeholder="存放位置" />
					</uni-forms-item>
					<uni-forms-item name="hobby" label="库存数量">
						<uni-easyinput disabled type="number" v-model="materialsInfo.locationNum" placeholder="库存数量" />
					</uni-forms-item>
					<uni-forms-item name="hobby" label="物料单位">
						<uni-easyinput disabled type="number" v-model="materialsInfo.materialsUnit" placeholder="物料单位" />
					</uni-forms-item>
					<uni-forms-item name="hobby" label="转移位置">
						<uni-data-picker :localdata="materialsAddrList" popup-title="请选位置信息" @change="onchange"></uni-data-picker>
					</uni-forms-item>
					<uni-forms-item name="hobby" label="转移数量">
						<uni-easyinput type="number" v-model="materialsInfo.locationNumSwitch" placeholder="转移数量" />
					</uni-forms-item>
				</uni-forms>
				<uni-row class="demo-uni-row" :gutter="20">
			        <uni-col :span="12">
			            <button @click="close" class="mms-btn-default">关闭</button>
			        </uni-col>
			        <uni-col :span="12">
			            <button @click="onsave" class="mms-btn">确定</button>
			        </uni-col>
			    </uni-row>
			</view>
		</uni-popup>
	</view>
</template>

<script>
	const util = require('../../static/js/util.js');
	export default {
		data() {
			return {
				userState: {},
				searchData:{}, 
				segmCurrent:0,
				segmItems:['存放位置','入库记录','出库记录'],
				materialsData:{},
				inOrderList:[],
				inOrderLength:0,
				outOrderList:[],
				outOrderLength:0,
				// 物料转仓参数
				materialsInfo:{},
				materialList:[],
				materialListLength:0,
				materialsAddrList:[],
				materialsAddrLength:0
			}
		},
		onLoad:function(option){
			const materialsNo = option.code;
			this.userState = util.xcStorage.getStorageSync('userState');
			this.getMaterials(materialsNo);
			this.getInOrder(materialsNo);
			this.getOutOrder(materialsNo); 
			
			this.getStoreHouseList();
			this.getMaterialList(materialsNo); 
		},
		created(){},
		methods: { 
			onClickItem(e){
				this.segmCurrent = e.currentIndex;
			},
			getMaterials(code){
				const _this = this;
				const url = util.xcRequestURL.WAREHOUSE.getMaterialList + "?token=" + _this.userState.token;
				util.xcRequest.xcGet(url,{materialsNo:code}, function(res) {
					// const { result } = res;
					if(res.status == 200){
						let resList = res.result;
						if(resList.length > 0){ 
						  _this.materialsData = res.result[0];
						} 
					}
				});
			},
			getInOrder(code){
				const _this = this;
				const url = util.xcRequestURL.WAREHOUSE.getInHouseRecordList + "?token=" + _this.userState.token;
				util.xcRequest.xcGet(url,{materialsNo:code}, function(res) {
					if(res.status == 200){ 
						let resList = res.result;
						if(resList.length > 0){
							_this.inOrderList = res.result;
						}
					}
				});
			},
			getOutOrder(code){
				const _this = this;
				const url = util.xcRequestURL.WAREHOUSE.getOutHouseRecordList + "?token=" + _this.userState.token;
				util.xcRequest.xcGet(url,{materialsNo:code}, function(res) {
					if(res.status == 200){ 
						let resList = res.result;
						if(resList.length > 0){
							_this.outOrderList = res.result;
						}
					}
				});
			},
			// 根据物料编号获取物料存放列表
			getMaterialList(materialsNo){
				const _this = this;
				const url = util.xcRequestURL.WAREHOUSE.getXcMaterialsList + "?token=" + _this.userState.token+"&materialsNo="+materialsNo;
				util.xcRequest.xcPost(url,{}, function(res) {
					if(res.status == 200){ 
						let resList = res.result;
						if(resList.length > 0){
							_this.materialList = res.result;
							_this.materialListLength = resList.length;
						}
					}
				});
			},
			// 获取物料信息位置信息列表
			getStoreHouseList(){
				var _this = this;
				const url = util.xcRequestURL.WAREHOUSE.getStoreHouseList + "?token=" + _this.userState.token;
				util.xcRequest.xcGet(url,{}, function(res) {
					if(res.status == 200){
						let materialsAddrList = res.result;
						if(materialsAddrList.length > 0){
							for(let i=0;i<materialsAddrList.length;i++){
								materialsAddrList[i].text = materialsAddrList[i].storehouseName;
								materialsAddrList[i].value = materialsAddrList[i].id;
							}
							_this.materialsAddrList = materialsAddrList;
							_this.materialsInfo.storehouseName1 = materialsAddrList[0].storehouseName;
							_this.materialsInfo.storehouseId1 = materialsAddrList[0].id;
						}
					}
				});
			},
			onOpenDetail(data,type){
				this.materialsInfo = data;
				this.$refs.popup.open();
			},
			close() {
			    // 为true的情况下，手动执行 close 才会关闭对话框
			    this.$refs.popup.close();
			},
			onsave(){
				// 判断转移物料数量是否小于原有数量 materialsInfo.offStockNum
				if(!this.materialsInfo.storehouseIdSwitch){
					uni.showToast({
						title: '请选择转移仓库！',
						duration:2200,
						icon: 'none'
					});
					return;
				}
				if(!this.materialsInfo.locationNumSwitch || parseFloat(this.materialsInfo.locationNumSwitch) > this.materialsInfo.locationNum || parseFloat(this.materialsInfo.locationNumSwitch) <= 0){
					uni.showToast({
						title: '操作数量不在范围内！',
						duration:2200,
						icon: 'none'
					});
					return;
				}
				this.materialsInfo.locationNumSwitch = parseFloat(this.materialsInfo.locationNumSwitch);
				var _this = this;
				let materInfo = _this.materialsInfo;
				const url = util.xcRequestURL.WAREHOUSE.switchLocation + "?token=" + _this.userState.token + "&materialsNo="+materInfo.materialsNo + "&storehouseId="+materInfo.storehouseId + "&locationNumSwitch="+materInfo.locationNumSwitch + "&storehouseIdSwitch="+materInfo.storehouseIdSwitch;
				util.xcRequest.xcPost(url,{}, function(res) {
					if(res.status == 200){
						uni.showToast({
							title: '操作成功！',
							duration:3000,
							icon: 'none'
						}); 
						_this.getMaterialList(_this.materialsNo);
					}
				}); 
				this.$refs.popup.close();
			},
			// 存放位置监听
			onchange(e) {
				// console.log(e.detail.value[0].text)
			    const data= e.detail.value;
				this.materialsInfo.storehouseIdSwitch = data[0].value;
			},
			onTimeChange(e){
				this.wareHousingTime = e;
				this.formData.wareHousingTime = e;
			}
		}
	}
</script>

<style>
	.container {
		font-size: 14px;
		line-height: 24px;
		box-sizing: border-box;
	}
	.section{
		padding:10px;
		background:#fff;
	}
	.section-info{
		font-size:14px;
		color:#888;
	}
	.section-title{
		position:relative;
		background:#fff;
		margin:0 0 12px;
		margin-bottom:6px;
		padding:6px 12px;
	}
	.section-title-text{
		padding-left:15px;
		border-left:3px solid #FFB800;
	}
	.title-right-txt{
		position:absolute;
		top:5px;
		right:20px;
		font-size:12px;
		color:#999;
	}
	.mms-content{
		padding:15px;
	}
	.mms-list{
		/* margin-top:15px; */
		padding:12px 0 12px;
		height:calc(100vh - 210px);
		overflow-y:auto;
	}
	.list-item-title{
		padding:0 0 5px 12px;
		margin-bottom:5px;
		font-size:15px;
		color:#444;
		box-sizing: border-box;
		border-bottom:1px solid #efefef;
	}
	.item-time{
		display:inline-block;
		float:right;
		font-size:12px;
		color:#888;
	}
	.list-title-icon{
		display:inline-block;
		position:absolute;
		left:15px;
		top:15px;
	}
	.list-item{
		position:relative;
		margin-bottom:10px;
		padding:12px;
		font-size:14px;
		line-height:1.8;
		color:#888;
		background:#fff;
		border-radius:8px;
	}
	.list-item-info{
		display:inline-block;
		width:calc(100% - 90px);
		vertical-align: top;
		word-break:break-all;
		overflow:hidden;
		color:#444;
	}
	.list-item-detail{
		position: absolute;
		top: 36px;
		right: 10px;
		padding: 2px 10px;
		color: #6d3b85;
		border: 1px solid #6d3b85;
		border-radius: 4px;
		font-size: 12px;
		text-align:center;
		z-index: 2;
	}
	.record-nolist{
		padding:30rpx;
		background:#fff;
		text-align:center;
		color:#666;
	}
</style>
